<template>
  <div class="container">
    <Header class="header" :propobj="propobj"/>
    <div class='container-home'>
      <Left class="aside" :blankflag="blankflag" />
      <div class = 'view'>
        <router-view v-on:blank-parent="blankParent"/>
      </div>
    </div>
  </div>
</template>
<script>
import Left from "../components/Left.vue";
import Header from "../components/Header.vue";
export default {
  created() {
    //用户信息获取
    let self = this;
    this.axios.get("/users/"+window.location.href.split("?")[1].split("=")[1]).then(res => {
      self.propobj.userinfo = res.data.data;
    });
  },
  data() {
    return {
      propobj: {
        userinfo: "",
        blankflag: true
      },
      blankflag: true
    };
  },
  components: {
    Left,
    Header
  },
  methods: {
    blankParent(message) {
      this.blankflag = false;
      this.propobj.blankflag = false;
      this.propobj.userinfo = false;
    }
  }
};
</script>
<style lang="less" scoped>
.container {
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-basis: auto;
  box-sizing: border-box;
  min-width: 0;
  height: 100vh;
  .header {
    background-color: #4c65a4;
    color: #333;
    line-height: 80px;
    width: 100%;
    height: 80px !important;
    box-sizing: border-box;
    flex-shrink: 0;
    padding: 0;
    box-shadow: 0px 5px 5px rgba(41, 43, 47, 0.3);
    z-index: 1;
  }
  .container-home {
    display: flex;
    flex: 1;
    flex-basis: auto;
    box-sizing: border-box;
    min-width: 0;
    .aside {
      background-color: #3a3f50;
      color: #333;
      box-sizing: border-box;
      flex-shrink: 0;
      overflow: auto;
    }
    .view {
      background-color: #e9eef3;
      color: #333;
      box-sizing: border-box;
      flex: 1;
      flex-basis: auto;
      padding: 30px 40px; //页面内容区域是padding：30px 40px
      overflow: auto;
    }
  }
}
</style>
<style>
.leftpart ul.el-menu {
  background-color: #3a4051;
}
.el-submenu__title i {
  color: #fff;
}
</style>





